<template>
	<div style="width: 100%;height: 100%; background: #fff;">
		<div class="header">
			<span class="vin" v-text="vin"></span>
			<span class="style"></span>
			<span class="return" @click="upper">×</span>
		</div>
		<div class="center">
			<div class="top">
				<span v-text="this.list.cateName" class="cateName"></span>:
				<span v-text="this.list.modelName" class="modelName"></span>
			</div>
			<div class="button">
				<span class="jact">经</span>
				<span v-text="this.list.retailerName" class="retailerName"></span>
			</div>

		</div>
		<div class="nav">
			<div class="navTop">
				<div class="navT_left">
					<img src="../../../common/img/icon-shou.png" />
					<span class="destination" v-text="destination"></span>
				</div>
				<div class="navRight">
					<div class="navR_left">
						<span class="orderSrcWhName" v-text="orderSrcWhName"></span>
						<span class="orderSrcCName" v-text="orderSrcCName"></span>
						<span>——</span>
						<span class="orderDestWhName" v-text="orderDestWhName"></span>
						<span class="orderDestCName" v-text="orderDestCName"></span>
					</div>
					<div class="navR_right">
						<span class="time">预计到达时间:</span>
						<span class="day" v-text="otd"></span>
					</div>
				</div>
			</div>
			<div class="navBut">
				<div class="navB_left">
					<ul class="ul">
						<li class="dayTime" v-for="o in time">
							{{o.time}}
							<span class="garden"></span>
						</li>
					</ul>
				</div>
				<div class="navB_right">
					<ul class="ul">
						<li class="details" v-for="o in time">
							<span v-text="o.orderStatus"></span>&nbsp;&nbsp;&nbsp;&nbsp;
							<span v-text="o.address"></span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
							<span>{{o.shipmentStatus|shipmentStatus}}</span>
						</li>
					</ul>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		filters: {
			shipmentStatus(value) {
				return '[' + value + ']'
			}
		},
		data() {
			return {
				title: '',
				vin: '',
				list: '',
				destination: '', //目的地
				orderSrcWhName: '', //出发仓库
				orderSrcCName: '', //出发市
				orderDestCName: '', //终点仓库
				orderDestWhName: '', //终点市
				otd: '', //预计达到时间
				time: '', //事件
			}
		},
		methods: {
			//初始化数据
			loadData() {
				var self = this;
				var p = {
					//					pageNum: this.pageNum,
					pageNum: 1,
					pageRows: 10,
					//					pageRows: this.pageRows,
					vins: [this.vin],
					operateCode: '',
					operateName: ''
				}
				//
				self.$api.post('/crm/crmOrder/getShipmentList', {
					vin: this.vin
				}, r => {
					this.destination = "[收货地址]" + " " + r.repData.orderDestAName + '/' + r.repData.orderDestPName + '/' + r.repData.orderDestCName
					this.orderSrcWhName = r.repData.orderSrcWhName
					this.orderSrcCName = '(' + r.repData.orderSrcCName + ')'
					this.orderDestWhName = r.repData.orderDestWhName
					this.orderDestCName = '(' + r.repData.orderDestCName + ')'
					this.otd = r.repData.otd
					this.time = r.repData.shipmentInfoList
				}, e => {
					this.$message({
						message: e.repData.repMsg,
						type: 'error'
					});
				})
				self.$api.post('crm/crmOrder/getOrderListByVin', p, r => {
					this.list = r.repData.list[0]
				}, e => {
					this.$message({
						message: e.repData.repMsg,
						type: 'error'
					});
				})
			},
			//返回
			upper() {
				this.$router.push({
					path: "/OrderQuery",
					query: {}
				})
			},
		},
		created() {
			this.vin = this.$route.query.vin
			console.log(this.vin)
			this.loadData();
		},
	}
</script>

<style scoped="scoped">
	.header {
		width: 100%;
		border-bottom: 1px solid #ECEDF2;
		height: 60px;
		position: relative;
	}
	
	.return {
		position: absolute;
		right: 20px;
		top: 10px;
		width: 15px;
		border: 1px solid #333;
		text-align: center;
		line-height: 15px;
		border-radius: 50%;
		font-size: 16px;
		cursor: pointer;
	}
	
	.vin {
		display: inline-block;
		height: 40px;
		width: 200px;
		text-indent: 40px;
		font-size: 14px;
		line-height: 60px;
		float: left;
	}
	
	.style {
		display: inline-block;
		width: 57px;
		height: 36px;
		background: url(../../../common/img/icon-dai.png);
		float: right;
		margin-right: 60px;
	}
	
	.center {
		width: 90%;
		height: 80px;
		padding-left: 40px;
	}
	
	.top {
		width: 100%;
		height: 40px;
	}
	
	.cateName {
		height: 40px;
		line-height: 40px;
		color: #666;
	}
	
	.retailerName {
		color: #666;
	}
	
	.button {
		height: 40px;
		line-height: 40px;
	}
	
	.modelName {
		height: 40px;
		line-height: 40px;
		color: #666;
	}
	
	.jact {
		display: inline-block;
		width: 20px;
		height: 20px;
		text-align: center;
		line-height: 20px;
		background: #FFAA1E;
		color: #fff;
		border-radius: 4px;
	}
	
	.nav {
		width: 94%;
		height: 70%;
		margin: 10px auto;
		border-radius: 20px;
		border: 2px solid #F7F7F7;
	}
	
	.navTop {
		width: 100%;
		height: 10%;
		border-bottom: 2px solid #F7F7F7;
	}
	
	.navBut {
		width: 100%;
		height: 89%;
	}
	
	.navT_left {
		width: 30%;
		height: 100%;
		float: left;
	}
	
	.navT_left img {
		margin: 7px 0 0 25px;
		float: left;
	}
	
	.navRight {
		width: 70%;
		height: 100%;
		float: right;
	}
	
	.navR_left {
		width: 66%;
		height: 100%;
		float: left;
		line-height: 50px;
		text-align: right;
		padding-right: 20px;
	}
	
	.navR_right {
		width: 30%;
		height: 100%;
		float: left;
	}
	
	.destination {
		display: inline-block;
		line-height: 50px;
		float: left;
		margin-left: 20px;
		color: #666;
	}
	
	.time {
		color: #666;
	}
	
	.orderSrcCName,
	.orderDestCName {
		font-size: 12px;
		color: #666;
	}
	
	.navR_right {
		line-height: 50px;
	}
	
	.day {
		color: #D9524F;
		font-weight: 900;
	}
	
	.navB_left {
		height: 100%;
		width: 20%;
		float: left;
	}
	
	.navB_right {
		height: 100%;
		width: 79%;
		float: left;
	}
	
	.dayTime {
		width: 95%;
		line-height: 60px;
		text-align: right;
		position: relative;
	}
	
	.navB_left ul:nth-child(1) {
		border-right: 1px solid #E4E4E4;
	}
	
	.ul {
		color: #A8A8A8;
	}
	
	.ul li:nth-child(1) {
		margin-top: 20px;
		color: #040404;
	}
	
	.garden {
		display: block;
		width: 12px;
		height: 12px;
		background: #E6E6E6;
		position: absolute;
		right: -8%;
		top: 23px;
		border-radius: 50%;
	}
	
	.details {
		width: 100%;
		line-height: 60px;
		text-indent: 20px;
	}
	
	.ul li:nth-child(1) .garden {
		background: #D9524F;
		border: 1px solid #fff;
		box-shadow: 0px 0px 4px 1px rgba(96, 199, 242, 0.5);
	}
	
	.details span:nth-child(1) {
		display: inline-block;
		min-width: 70px;
		text-align: center;
	}
	
	.details span:nth-child(2) {
		display: inline-block;
		min-width: 200px;
		text-align: center;
	}
</style>


